

.reg1{
  font-size:30px;
  margin:auto;
  text-align:center;
  color:white;
}
.login-page {
  width: 360px;
  padding: 11% 0 0;
  margin: auto;
}
.form1 {
  position: relative;
  z-index: 1;
  background: #FFFFFF;
  max-width: 360px;
  margin: 0 auto 100px;
  padding: 45px;
  text-align: center;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}
.form1 input {
  outline: 0;
  background: #f2f2f2;
  width: 100%;
  border: 0;
  margin: 0 0 15px;
  padding: 15px;
  box-sizing: border-box;
  font-size: 14px;
  text-transform: none !important;
}
.form1 button {
  text-transform: uppercase;
  outline: 0;
  background: #4CAF50;
  width: 100%;
  border: 0;
  padding: 15px;
  color: #FFFFFF;
  font-size: 14px;
  -webkit-transition: all 0.3 ease;
  transition: all 0.3 ease;
  cursor: pointer;
}
.form1 button:hover,.form1 button:active,.form1 button:focus {
  background: #43A047;
}
.form1 .message1 {
  margin: 15px 0 0;
  color: rgb(98, 93, 93);
  font-size: 15px;
}
.form1 .message1 a {
  color: #177b1b;
  text-decoration: none;
}
.form1 .register-form1 {
  display: none;
}
