* 
{
  font-family: 'Poppins', sans-serif;
  margin: 0;
  padding: 0;
  outline: none;
  border: none;
  text-decoration: none !important;
  text-transform: capitalize;
  /* transition: .2s linear; */
}

.btn-login {
  text-decoration: none !important;
  margin-top: 0;
  padding: .2rem 1.5rem !important;
  border-radius: 5rem;
  border-top-left-radius: 0;
  border: 0.2rem solid black;
  cursor: pointer;
  background: white;
  color: white;
  font-size: 1.7rem;
  overflow: hidden;
  z-index: 0;
}

.btn-login:hover {
  background-color: #130f40;
  color: #e67e22 !important;
}

.colo:hover {
  color: #e67e22 !important;
}

html {
  font-size: 62.5%;
  overflow-x: hidden;
  scroll-behavior: smooth;
  scroll-padding-top: 4rem;
} 

section {
  padding: 5rem 9%;
}

.heading {
  padding-bottom: 2rem;
  text-align: center;
  font-size: 3.5rem;
  color: #130f40;
}

.heading span {
  color: #e67e22;
}

.btn2 {
  text-decoration: none;
  display: inline-block;
  margin-top: 1rem;
  padding: .8rem 2.8rem;
  border-radius: 5rem;
  border-top-left-radius: 0;
  border: 0.2rem solid #130f40;
  cursor: pointer;
  background: none;
  color: #130f40;
  font-size: 1.7rem;
  overflow: hidden;
  z-index: 0;
  position: relative;
}

.btn1 {
  text-decoration: none;
  display: inline-block;
  margin-top: 1rem;
  padding: .8rem 2.8rem;
  border-radius: 5rem;
  border-top-left-radius: 0;
  border: 0.2rem solid white;
  cursor: pointer;
  background: none;
  color: white;
  font-size: 1.7rem;
  overflow: hidden;
  z-index: 0;
  position: relative;
  text-decoration: none;
}

.btn1:hover{
  text-decoration: none;
  color: black;
  background-color: white;
  text-decoration: none;
}


.btn2:hover {
  text-decoration: none;
  color: white;
  background-color: #130f40 ;
}

.back-to-home-button{
  color:#130f40 !important;

}
.back-to-home-button:hover{
  color: #e67e22 !important;
}
